
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'


export default {
data() {
return {
userInfo: {
nicknames: "",
sex: "",
birthdays: "",
remarks: ""
}
};
},
components: {
XHeader,
Flexbox,
FlexboxItem,
Group,
XInput,
PopupPicker,
Datetime,
XAddress,
XTextarea
},
created: function() {
this.userInfo = JSON.parse(localStorage.getItem('userInfo'));

if(!this.userInfo.logos) {
this.userInfo.logos = "src/assets/img/test/avatar.png";
}

if(!this.userInfo.nicknames) {
this.userInfo.nicknames = "";
}

if(!this.userInfo.gender) {
this.userInfo.sex = "";
}else if(this.userInfo.gender==1){
this.userInfo.sex = "男";
}else {
this.userInfo.sex = "女";
}

if(this.userInfo.birthdays == '0000-00-00') {
this.userInfo.birthdays = "";
}

if(!this.userInfo.remarks) {
this.userInfo.remarks = "";
}
},
methods: {
async commit() {
let data = await util.getData({
url: `/users/updates`,
method: "post",
param: {
loginid: this.userInfo.id,
nicknames: this.userInfo.nicknames,
genders: this.userInfo.sex == "男" ? 1 : 2,
logos: this.userInfo.logos,
birthdays: this.userInfo.birthdays,
remarks: this.userInfo.remarks,
}
});
this.$vux.toast.show({
text: data,
type: "text",
onHide: () => {
this.$router.push({
path: '/page/UserIndex'
});
}
});
}
}
};



